@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,400;0,700;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

._recipes {
  margin: 5% auto;
  padding: 5%;
  background-color: #008080b4;
  border-radius: 150px;
  overflow: hidden;
}

.img {
  overflow: hidden;
  display: inline-block;
  border: 2px solid #004c4c;
  border-radius: 30px;
}

._image:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

._image {
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

._label {
  font-size: 2.5rem;
}

._calories,
._ingredients {
  font-size: 1.5rem;
}

._ingredients_box {
  background: #b2d8d87c;
  width: 80%;
  margin: 1% auto;
  padding: 1%;
  border-radius: 50px;
}

._ingredients_tit {
  font-size: 2rem;
}

._ingredients {
  text-align: center;
  padding: 0 5%;
}

.Home {
  text-align: center;
  color: #004c4c;
}

.search-form {
  margin: 2% auto;
}

.search-bar {
  text-indent: 10px;
  width: 250px;
  height: 30px;
  margin-right: 10px;
  border: 2px solid #006666;
  background-color: #b2d8d8;
}

.search-button {
  width: 80px;
  height: 30px;
  border: 2px solid #004c4c;
  background-color: #006666;
  color: #b2d8d8;
  font-weight: bold;
}

._recipes_detail {
  margin: 4%;
  padding: 4%;
  display: inline-block;
  background-color: #008080b4;
  border-radius: 150px;
  overflow: hidden;
  text-align: center;
}

.label_ingBox {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.img_detail {
  overflow: hidden;
  display: inline-block;
  border: 2px solid #004c4c;
  border-radius: 30px;
  width: 25%;
}

._image_detail {
  width: 100%;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

._image_detail:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.rightBox {
  /* width: 60%; */
}

h3 {
  font-size: 2rem;
}

span {
  font-size: 1.2rem;
}

.health_nutrients {
  background: #b2d8d87c;
  margin: 3% auto;
  padding: 1%;
  border-radius: 30px;
  width: 95%;
}

._url_detail {
  margin-top: 2%;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto Mono", monospace;
  background-color: #b2d8d8;
}

html {
  font-size: 10px;
}

a {
  color: inherit;
  text-decoration: inherit;
  display: block;
  width: 60%;
  margin: 0 auto;
}

img {
  vertical-align: middle;
}

a img {
  border: none;
}

li {
  list-style: none;
}

